document.getElementById("adventurePredictionForm").addEventListener("submit", function(event) {
  event.preventDefault();
  predictAdventure();
});

document.getElementById("closePopup").addEventListener("click", function() {
  document.getElementById("popup").style.display = "none";
});

function predictAdventure() {
  let name = document.getElementById("name").value;
  let country = document.getElementById("country").value;

  let adventureScore = Math.floor(Math.random() * 100) + 1;

  let adventureMessage = generateAdventureMessage(adventureScore);
  document.getElementById("adventureChance").textContent = name + ",there is a " + adventureScore + "% probability that you will have an adventure in " + country + "." + adventureMessage;

  document.getElementById("popup").style.display = "block";
}

function generateAdventureMessage(adventureScore) {
  if (adventureScore >= 80) {
      return "Book a ticket ASAP! ðŸ¥°";
  } else if (adventureScore >= 40) {
      return "You might get lucky:)";
  } else {
      return " Hmm, maybe you should consider another destination?";
  }
}
